@import "mixins";

.search-window{
	width: 100%;
	max-width: 1118px;
	margin: 0 auto;
	padding-top: 160px;
	@include mob {
		padding: 150px 16px 16px;
	}
}
.search-window-form{
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	
	width: 100%;
	padding-bottom: 18px;
	border-bottom: 2px solid var(--color-white);
	@include mob {
		align-items: flex-start;
		padding-bottom: 16px;
		border-bottom: 1px solid var(--color-white);
	}

	.search__field{
		flex: 1 0 auto;
		max-width: none;
	    min-width: auto;
		padding: 0;
		border: none;
		outline: none;
		border-radius: 0;
		background: transparent;

		color: var(--color-white);
		font-feature-settings: 'clig' off, 'liga' off;
		font-family: 'Manrope';
		font-size: 48px;
		font-style: normal;
		font-weight: 600;
		line-height: 125%;
		letter-spacing: -1px;
		@include mob {
			font-size: 18px;
			font-style: normal;
			font-weight: 500;
			line-height: 28;
			letter-spacing: 0;
		}

		&::placeholder{
			color: var(--999999-silver);
		}
		&.active{

		}
	}
	.search__btn{
		width: 50px;
		height: 50px;

		margin-left: 50px;

		border-radius: 25px;
		background: var(--color-white-60);
		box-shadow: 7px 10px 18px 0px rgba(0, 0, 0, 0.15);
		backdrop-filter: blur(5px);

		@include mob {
			width: 40px;
			height: 40px;
			margin-left: 20px;
		}

		svg{
			width: 23px;
			height: 15px;
			use{
				fill: var(--color-text-hover);
			}
		}
	}
	
	&.active{
		.search__btn{
			background: var(--color-white);
		}
	}
}

.search-suggest{
	margin-top: 40px;
	@include mob {
		margin-top: 80px;
	}
	li{
		margin-bottom: 20px;
		&:last-child{
			margin-bottom: 0
		}
		a{
			display: inline-block;

			color: var(--999999-silver);
			font-feature-settings: 'clig' off, 'liga' off;
			font-family: 'Manrope';
			font-size: 24px;
			font-style: normal;
			font-weight: 500;
			line-height: 32px;
			@include mob {
				font-size: 18px;
				line-height: 28px;
			}

			.highlight{
				color: var(--color-white);
			}
			&:hover{
				color: var(--color-text-hover);

				.highlight{
					color: var(--color-text-hover);
				}	
			}
		}
	}
}
.search-empty{
	margin-top: 40px;
	
	color: var(--color-white);
	font-feature-settings: 'clig' off, 'liga' off;
	font-family: 'Manrope';
	font-size: 24px;
	font-style: normal;
	font-weight: 500;
	line-height: 32px;
	@include mob {
		font-size: 18px;
		line-height: 28px;
	}
}